<script>
import Headerlogo from './components/Headerlogo.vue'


export default ({
   components: {
    Headerlogo
    
  },
  data() {
    return{
      openMenu:false,
      openMenuAccount:false
    }
    
  },
  methods:{
    show_menu(){
      if(this.openMenu ==true || this.openMenuAccount ==true){
        this.openMenu = false
        this.openMenuAccount = false;
      }else{
        this.openMenu = true
      }
    },
    show_menu_account(){
       if(this.openMenuAccount ==true || this.openMenu ==true){
        this.openMenuAccount = false;
        this.openMenu = false;
      }else{
        this.openMenuAccount = true
      }
    }
  }
})
</script>


